*{
  font-family: jura;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
background: linear-gradient(to right, #8360c3, #2ebf91);
font-family: jura;
}

label{
  padding-right: 0.5rem;
  color: White;
  text-transform: uppercase;

}

.confirmation{
  color: white;
  font-size: 1.4rem;
  text-align: center;
}

.btn{
  border: none;
  padding: 20px;
  color: white;
  font-size: 1.2rem;
  border-radius: 3px;
  background-color: #8360c3;
  transition: all 0.3s ease-in-out;
  box-shadow: -1px 13px 44px -19px rgba(0,0,0,0.40);
}


label{
  padding-bottom: 5px;
}

.btn:hover{
  background-color:#2ebf91;
  color:#8360c3
}
.wrapper{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  
  
  
}
textarea{
  overflow: auto;
  border: none;
  border-radius: 3px;
  box-shadow: -1px 13px 44px -19px rgba(0,0,0,0.49);
  padding: 10px
}

textarea[placeholder]{
  font-size: 1.2rem;
  padding-left: 10px;
  resize: none;
  width: 400px;
}
.form-field{
  display:flex;
  flex-direction: column;
  padding-bottom: 20px;
}

label{
  font-weight: bold;
  letter-spacing: 0.1rem;
}

select{
  height: 40px;
  border: none;
  border-radius: 3px;
  box-shadow: -1px 13px 44px -19px rgba(0,0,0,0.49);
  font-size: 1.2rem;
}

textarea:focus, input:focus{
  outline: none;
}



input{
  border: none;
  border-radius: 3px;
  height: 40px;
  box-shadow: -1px 13px 44px -19px rgba(0,0,0,0.49);
 
}

input[type=text]{
font-size: 1.2rem;
padding-left: 3px;
width: 100%
}